.container {
  display: flex;
  position: fixed;
  height: 100vh;
  flex-direction: column;
  padding: 15px;
  padding-bottom: 0; }

.containerRight {
  display: flex;
  position: fixed;
  right: 0;
  height: 100vh;
  flex-direction: column;
  padding: 15px;
  padding-bottom: 0; }

.division {
  padding: 15px;
  width: 350px;
  background-color: #32393d;
  position: relative;
  display: flex;
  flex-direction: column;
  border-radius: 10px;
  margin-bottom: 15px; }

.scrollable {
  overflow-y: auto;
  height: 200px;
  flex-grow: 100; }

.growdiv {
  flex-grow: 100; }

.sidebar {
  width: 100%; }
  .sidebarEl {
    padding: 0.2rem 0;
    font-size: 16px;
    color: white;
    margin: 0.2rem 0;
    display: flex;
    width: 100%;
    align-items: center; }
    .sidebarEl:hover {
      background-color: #1e2024;
      cursor: pointer;
      color: #f2f1ef;
      border-radius: 6px; }
  .sidebar_icon {
    font-size: 15px;
    margin: 10px 15px;
    color: white; }

.user {
  position: relative;
  display: flex;
  vertical-align: middle;
  margin: 0 0 15px 0; }

.ppicture {
  height: 70px;
  width: 70px;
  border-radius: 35px;
  margin-right: 15px;
  object-fit: cover; }

.username {
  display: block;
  color: white;
  font-size: 28px;
  line-height: 37px; }

.activity {
  display: block;
  color: #c6c6d9;
  font-size: 1.3rem;
  line-height: 27px; }

.online {
  position: absolute;
  top: 45px;
  left: 45px;
  height: 25px;
  width: 25px;
  border-radius: 100px;
  background-color: #66bb6a;
  border: solid #32393d 7px; }

.friend {
  position: relative; }

.fppicture {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  object-fit: cover; }

.fonline {
  position: absolute;
  top: 40px;
  left: 40px;
  height: 20px;
  width: 20px;
  border-radius: 100px;
  background-color: #66bb6a;
  border: solid #32393d 5px; }

.friend {
  padding: 10px;
  display: flex;
  align-items: center; }
  .friend:hover {
    background-color: #1e2024;
    border-radius: 5px; }
    .friend:hover .fonline {
      border: solid #1e2024 5px; }

.fusername {
  display: block;
  color: white;
  font-size: 1.45rem;
  line-height: 15px; }

.factivity {
  display: block;
  color: #c6c6d9;
  font-size: 1.3rem;
  line-height: 15px; }

.fabout {
  margin-left: 10px; }

.yellow {
  background-color: #ffec40; }

.red {
  background-color: #ff3131; }

.green {
  background-color: #66bb6a; }

.black {
  background-color: #585e6a; }

.gamediv {
  align-items: center;
  display: flex;
  padding: 10px; }
  .gamediv:hover {
    background-color: #1e2024;
    border-radius: 5px; }

.gameicon {
  width: 50px;
  height: 50px;
  margin-right: 15px;
  border-radius: 50%;
  object-fit: cover; }

.gamename {
  color: #f2f1ef;
  font-size: 1.5rem;
  flex-grow: 100; }

.playicon {
  width: 3rem;
  height: 3rem;
  background-color: #ff9a60;
  border-radius: 50%;
  margin-right: 1.5rem; }

.playicon i {
  margin: 1rem 1rem 1rem 1.1rem;
  font-size: 1rem;
  color: white; }

.scrollable::-webkit-scrollbar-track {
  border-radius: 100px;
  background-color: #32393d; }

.scrollable::-webkit-scrollbar {
  width: 7px;
  background-color: #32393d;
  border-radius: 100px; }

.scrollable::-webkit-scrollbar-thumb {
  background-color: #1e2024;
  border-radius: 100px; }
